import React, { Component } from "react";
import "./App.css";
import Circles from "../src/components/Circles/Circles";
import CircleSelector from "../src/components/CircleSelector/CircleSelector";

const buttons = [0, 1, 2, 3];
const circles = [0, 1, 2, 3];

class App extends Component {
  state = {
    selButtonIdx: 0,
    selCircleIdx: 0,
  };

  handleButtonSelection = (buttonIdx, circleIdx) => {
    circleIdx = buttonIdx;
    this.setState({
      selButtonIdx: buttonIdx,
      selCircleIdx: circleIdx,
    });
  };

  render() {
    return (
      <div className="App">
        <header className="App-header">UNIT 4 FINAL ASSESSMENT</header>
        <main>
          <CircleSelector
            handleButtonSelection={this.handleButtonSelection}
            buttons={buttons}
            circles={circles}
            selCircleIdx={this.state.selCircleIdx}
            selButtonIdx={this.state.selButtonIdx}
          />
          <Circles
            selButtonIdx={this.state.selButtonIdx}
            selCircleIdx={this.state.selCircleIdx}
            handleButtonSelection={this.handleButtonSelection}
            circle={this.state.circle}
            circles={circles}
          />
        </main>
      </div>
    );
  }
}

export default App;
