import React from "react";
import "./CircleSelector.css";

const CircleSelector = (props) => {
  return (
    <div className="CircleSelector">
      {props.buttons.map((button, idx, circleIdx) => (
        <button
          key={button}
          className={props.selButtonIdx === idx ? "selected" : null}
          onClick={() => props.handleButtonSelection(idx)}
        >
          {props.selButtonIdx === idx
            ? `CIRCLE ${idx + 1} SELECTED`
            : `SELECT CIRCLE ${idx + 1}`}
        </button>
      ))}
    </div>
  );
};

export default CircleSelector;
