import React from "react";
import "./Circles.css";

const Circles = (props) => {
  return (
    <div className="Circles">
      {props.circles.map((circle, idx) => (
        <div
          key={circle}
          style={{
            backgroundColor: props.selButtonIdx === idx ? "white" : "limegreen",
            borderColor: props.selButtonIdx === idx ? "purple" : "limegreen",
            color: props.selButtonIdx === idx ? "purple" : "white",
          }}
        >
          {idx + 1}
        </div>
      ))}
    </div>
  );
};

export default Circles;
